* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
  }
  
  body {
    width:100%;
    --s: 400px; /* control the size */
    --c1: black;
    --c2: var(--color-bg);
    
    --_s: calc(2*var(--s)) calc(2*var(--s));
    --_g: var(--_s) conic-gradient(at 40% 40%,#0000 75%,var(--c1) 0);
    --_p: var(--_s) conic-gradient(at 20% 20%,#0000 75%,var(--c2) 0);
    background:
      calc( .9*var(--s)) calc( .9*var(--s))/var(--_p),
      calc(-.1*var(--s)) calc(-.1*var(--s))/var(--_p),
      calc( .7*var(--s)) calc( .7*var(--s))/var(--_g),
      calc(-.3*var(--s)) calc(-.3*var(--s))/var(--_g),
      conic-gradient(from 90deg at 20% 20%,var(--c2) 25%,var(--c1) 0) 
        0 0/var(--s) var(--s); 
    background-attachment: fixed;
    background-repeat: repeat;
    animation: m 30s infinite;
    }
    @keyframes m {
      0% {
       background-position: 
        calc( .9*var(--s)) calc( .9*var(--s)),
        calc(-.1*var(--s)) calc(-.1*var(--s)),
        calc( .7*var(--s)) calc( .7*var(--s)),
        calc(-.3*var(--s)) calc(-.3*var(--s)),0 0
      }
      25% {
       background-position: 
        calc(1.9*var(--s)) calc( .9*var(--s)),
        calc(-1.1*var(--s)) calc(-.1*var(--s)),
        calc(1.7*var(--s)) calc( .7*var(--s)),
        calc(-1.3*var(--s)) calc(-.3*var(--s)),0 0
      }
      50% {
       background-position: 
        calc(1.9*var(--s)) calc(-.1*var(--s)),
        calc(-1.1*var(--s)) calc( .9*var(--s)),
        calc(1.7*var(--s)) calc(-.3*var(--s)),
        calc(-1.3*var(--s)) calc( .7*var(--s)),0 0
      }
      75% {
       background-position: 
        calc(2.9*var(--s)) calc(-.1*var(--s)),
        calc(-2.1*var(--s)) calc( .9*var(--s)),
        calc(2.7*var(--s)) calc(-.3*var(--s)),
        calc(-2.3*var(--s)) calc( .7*var(--s)),0 0
      }
      100% {
       background-position: 
        calc(2.9*var(--s)) calc(-1.1*var(--s)),
        calc(-2.1*var(--s)) calc(1.9*var(--s)),
        calc(2.7*var(--s)) calc(-1.3*var(--s)),
        calc(-2.3*var(--s)) calc(1.7*var(--s)),0 0
      }
    
  } 

  .body::-webkit-scrollbar{
    display:none;
  }
  
  a {
    color: unset;
    text-decoration: none;
  }
  
  .gradient__bg {
    position:sticky;
    animation: gradient-bg-animation 12s infinite alternate linear;
    background: radial-gradient(circle at top, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 45%);
  }

  @keyframes gradient-bg-animation {
    from { /* radial-gradient(farthest-corner at top right, ..) */
      background-position:left top;
      background-size:100% 100%;
    
    }
    49.9% {
      background-position:left top;  
    }
    50% { /* radial-gradient(farthest-corner at top center, ..) */
      background-size:100% 100%;
    }
    50.1% {
      background-position:right top; 
    }
    to { /* radial-gradient(farthest-corner at top left, ..) */
      background-position:right top;
      background-size:100% 100%;
    }
  }
  
  .gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  .section__padding {
    padding: 4rem 6rem;
  }
  
  .section__margin {
    margin: 4rem 6rem;
  }

  .waves {
    position:absolute;
    width: 100%;
    left: 0;
    height:100%;
    margin-bottom:-7px; /*Fix for safari gap*/
    min-height:100px;
    max-height:150px;
  }

/* Animation */

.parallax > use {
    animation: move-forever 25s cubic-bezier(.55,.5,.45,.5)     infinite;
  }
  .parallax > use:nth-child(1) {
    animation-delay: -2s;
    animation-duration: 7s;
  }
  .parallax > use:nth-child(2) {
    animation-delay: -3s;
    animation-duration: 10s;
  }
  .parallax > use:nth-child(3) {
    animation-delay: -4s;
    animation-duration: 13s;
  }
  .parallax > use:nth-child(4) {
    animation-delay: -5s;
    animation-duration: 20s;
  }

  @keyframes move-forever {
    0% {
     transform: translate3d(-90px,0,0);
    }
    100% { 
      transform: translate3d(85px,0,0);
    }
  }

  /*Shrinking for mobile*/
  @media (max-width: 768px) {
    .waves {
      height:40px;
      min-height:40px;
    }
  }
  
