.creo__creo {
    display: flex;
    flex-direction: row;

    padding: 10em 20em;
    margin: 2rem 0;
}

.creo__creo-left {
    padding-left: 6em;
}

.creo__creo-left, .creo__creo-right {
    width:50%;
    flex:row;
}

.creo__creo-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
    max-width: 800px;
}

.creo__creo-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
}

.creo__creo-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
    margin-top: 1rem;
}

/* Customizing feature component as per needs */
.creo__creo-container .creo__features-container__feature {
    flex: 1;
    min-width: 200px;
    display: unset;
    flex-direction: column;
}

/* Customizing feature component as per needs */
.creo__creo-container .creo__features-container_feature-text {
    margin-top: 2rem;
    text-align: center;
}

.creo__creo-feature-1 {
    z-index: 1;
}

.creo__creo-feature-1 .creo__features-container_feature-text {
    height: fit-content;
}

.creo__creo-feature-1 .creo__features-container_feature-price {
    left: -15%;
}

.creo__creo-feature-1 img {
    margin-top: 0.5em;
}

@media screen and (max-width: 1900px) {
    .creo__creo {
        padding: 2em 5em;
    }

    .creo__creo-feature-1 .creo__features-container_feature-text {
        height:fit-content;
    }

    .creo__creo-feature-1 .creo__features-container_feature-price {
        height:fit-content;
    }

}

@media screen and (max-width: 1070px) {
    .creo__creo {
        padding: 0;
    }
    
    .creo__creo-right iframe {
        width:100%;
    }

}

@media screen and (max-width: 850px) {
    .creo__creo {
        flex-direction: column;
        height: fit-content;
        width:100%;
    }

    .creo__creo-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .creo__creo-left .creo__creo-feature-1 {
        margin-left:8em;
    }

    .creo__creo-left, .creo__creo-right {
        width:100%;
        flex:column;
    }   

    .creo__creo-heading p {
        margin-top: 1rem;
    }
    
    .creo__creo-container {
        grid-template-columns: repeat(2, 1fr);
    }

    .creo__creo-right iframe {
        margin-top: 10em;
        height:20em;
    }

}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .creo__creo-feature-1 .creo__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .creo__creo-feature-1 .creo__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .creo__creo-left .creo__creo-feature-1 {
        margin-left:3em;
    }

    .creo__creo-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }

}

@media screen and (max-width: 560px) {
    .creo__creo-container {
        grid-template-columns: repeat(1, 1fr);
    }
    .creo__creo-right iframe {
        margin-top: 15em;
        height:20em;
    }
    .creo__creo-left {
        padding: 0 5em;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .creo__creo-container .creo__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
    
}