.creo__tarifs {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;

    background: linear-gradient(-45deg, #b85b3f, #9b2753, #19799c, #178a6f);
	background-size: 100% 100%;
	animation: gradient 15s ease infinite;
	height: fit-content;
    padding: 10em 15em;
    z-index: 1;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes cube {
    from {
        transform: scale(0) rotate(0deg) translate(-50%, -50%);
        opacity: 1;
    }
    to {
        transform: scale(20) rotate(960deg) translate(-50%, -50%);
        opacity: 0;
    }
}

.background {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    margin: 0;
    padding: 0;
    background: transparent;
    overflow: hidden;
    opacity: 0.3;
    z-index: 0;
}

.background li {
    position: absolute;
    width: 8px;
    height: 8px;
    
    border-radius: 0em;
    border: solid 2px #1e3f82;
    color: transparent;
    transform-origin: top left;
    transform: scale(0) rotate(0deg) translate(-50%, -50%);
    animation: cube 12s ease-in forwards infinite;
    z-index: 2;
}

