.creo__tarifs-heading {
    z-index: 1;
}

.creo__tarifs-heading h1 {
    padding: 3em;
    margin: 1em 0;
    text-align: center;
    font-size: 40px;
    line-height: 60px;
    font-weight: 800;
    font-family: var(--font-family);
}

.creo__tarifs-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
}

.creo__tarifs-container {
    display: grid;
    grid-gap: 2em;
    grid-template-columns: repeat(4, 1fr);
    text-align: center;
    margin: 3em;
    z-index: 1;
}

/* Customizing feature component as per needs */
.creo__tarifs-container .creo__features-container__feature {
    flex: 1;
    min-width: 200px;
    display: unset;
    flex-direction: column;
    cursor: pointer;
}

/* Customizing feature component as per needs */
.creo__tarifs-container .creo__features-container_feature-text {
    margin-top: 2rem;
    text-align: center;
}

@media screen and (max-width: 1920px) {
    .creo__tarifs {
        padding: 2em;
    }
    .creo__tarifs-container {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media screen and (max-width: 1400px) {
    .creo__features-container__feature {
        margin-bottom: 10em;
    }
}


@media screen and (max-width: 1120px) {
    .creo__tarifs {
        padding: 0;
    }
    .creo__features-container__feature {
        margin-bottom: 20em;
    }
    .creo__features-container_feature-price {
        transform: skew(-10deg);
        left:-5%;
    }
}

@media screen and (max-width: 850px) {
    .creo__tarifs-heading {
        /* flex-direction: column; */
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .creo__tarifs-heading p {
        margin-top: 1rem;
    }
    
    .creo__tarifs-container {
        grid-template-columns: repeat(2, 1fr);
    }
    .creo__features-container__feature {
        margin-bottom: 5em;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .creo__creo-feature .creo__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .creo__tarifs-feature .creo__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .creo__tarifs-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 560px) {
    .creo__tarifs-container {
        grid-template-columns: repeat(1, 1fr);
    }
    .creo__features-container__feature {
        margin-bottom: 3em;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .creo__creo-container .creo__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
    
}