@import url(https://fonts.googleapis.com/css?family=Righteous);
.creo__header {
  width: 100%;
  display: flex;
  flex-direction: row;
  color: #FFF;
}

.creo__header-content_left {
  flex: 1;
  display: flex;
  justify-content: center;
  width: 50%;
  margin-right: 5rem;
}

.creo__header-text {
  display: flex;
  color: mediumaquamarine;
  font-family: "Righteous", serif;
  text-align: justify;
  text-transform: uppercase;
  font-size: 30px;
  text-shadow: 0.08em 0.08em 0 hsla(230, 40%, 50%, 0.5);
  margin-left: 5em;
  margin-bottom: 5em;
  width: 80%;
}

/* 
** Change this if you need to modify
** the amount of lines displayed
*/
/*
** Leave these unless you want
** to open a whole can of worms
*/
/*
** Apply styles to the parent element
*/
.Words {
  flex: 1;
  margin-left: -10%;
  padding: 40px;
  font-family: "Source Sans Pro", Helvetica, Arial, sans-serif;
  font-size: 60px;
  font-weight: 500;
  letter-spacing: 2px;
  text-transform: uppercase;
  transform: translate3d(0, 0, 0);
  font-kerning: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-font-kerning: normal;
  -webkit-text-size-adjust: 100%;
}

/*
** Apply common styles to each line of text
*/
.Words-line {
  height: 70px;
  overflow: hidden;
  position: relative;
}
.Words-line:nth-child(odd) {
  transform: skew(60deg, -30deg) scaleY(0.66667);
}
.Words-line:nth-child(even) {
  transform: skew(0deg, -30deg) scaleY(1.33333);
}
.Words-line:nth-child(1) {
  left: 29px;
}
.Words-line:nth-child(2) {
  left: 58px;
}
.Words-line:nth-child(3) {
  left: 87px;
}
.Words-line:nth-child(4) {
  left: 116px;
}
.Words-line:nth-child(5) {
  left: 145px;
}
.Words-line:nth-child(6) {
  left: 174px;
}
.Words-line:nth-child(7) {
  left: 203px;
}

/*
** Fine-grained text styles
*/
.Words-line p {
  height: 70px;
  line-height: 20px;
  padding: 0 5px;
  transition: all 0.4s ease-in-out;
  transform: translate3d(0, 0, 0);
  vertical-align: top;
  white-space: nowrap;
}

/*
** The hover interaction
*/
.Words:hover p {
  transform: translate3d(0, -70px, 0);
}

.creo__header-image {
  flex: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  animation: zoomInOut 40s linear infinite;
}

.creo__header-image img {
  width: 100%;
  height: 100%;
  border-radius: 6em;
}

@keyframes zoomInOut {
  0% {
    transform: scale(0.6);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.6);
  }
}
.img span {
  width: 50%;
  height: 50%;
  position: absolute;
}

.img span:first-child,
.img span:nth-child(2) {
  top: 0;
}

.img span:first-child,
.img span:nth-child(3) {
  left: 0;
}

.img span:nth-child(2),
.img span:nth-child(4) {
  right: 0;
}

.img span:nth-child(3),
.img span:nth-child(4) {
  bottom: 0;
}

#grid {
  display: grid;
  grid-auto-rows: minmax(auto, auto);
  width: auto;
  position: relative;
  transform: translateX(-25% -40%);
}

#grid > a {
  position: relative;
  transform-style: preserve-3d;
}

#grid > a:hover {
  cursor: pointer;
}

#grid > a:hover .title:after {
  background-position: 0% 0%;
}

#grid > a .img {
  width: 60%;
  position: relative;
  box-shadow: 4em -2em rgba(35, 139, 117, 0.27), -4em 2em rgba(100, 29, 109, 0.27);
  border-radius: 40em;
}

#grid > a .img > span {
  z-index: 2;
}

#grid > a .img > span ~ img {
  z-index: 1;
  transition: 0.3s;
  width: 100%;
}

#grid > a .img > span:first-child:hover ~ img {
  transform: perspective(800px) rotateX(20deg) rotateY(-20deg);
}

#grid > a .img > span:nth-child(2):hover ~ img {
  transform: perspective(800px) rotateX(20deg) rotateY(20deg);
}

#grid > a .img > span:nth-child(3):hover ~ img {
  transform: perspective(800px) rotateX(-20deg) rotateY(-20deg);
}

#grid > a .img > span:nth-child(4):hover ~ img {
  transform: perspective(800px) rotateX(-20deg) rotateY(20deg);
}

@media screen and (min-width: 1650px) {
  .creo__header-image {
    left: 20%;
  }
}
@media screen and (max-width: 1650px) {
  .creo__header {
    flex-direction: column;
  }
  .creo__header-text {
    margin-top: 2em;
    margin-left: 0;
  }
  .creo__header-content_left {
    align-self: center;
  }
  .creo__header-content_right {
    margin-top: 8em;
    margin-left: 20%;
  }
}
@media screen and (max-width: 1250px) {
  .Words {
    margin-top: 3em;
    margin-left: -50%;
  }
  .creo__header-text {
    font-size: 20px;
    margin-top: 10em;
    width: 100%;
  }
  .creo__header-content_left {
    margin-left: 2em;
  }
  .creo__header-content_right {
    margin-left: 0;
  }
  .creo__header-image {
    left: 12%;
  }
}
@media screen and (max-width: 950px) {
  .creo__header-content_left {
    margin-top: 10%;
    margin-left: -10%;
  }
  .Words {
    font-size: 35px;
  }
  .creo__header-text {
    font-size: 18px;
    margin-top: 10em;
    margin-left: 0;
  }
}
@media screen and (max-width: 650px) {
  .creo__header {
    margin-top: 0rem;
  }
  .creo__header-content_left {
    margin-top: 8em;
  }
  .Words {
    font-size: 20px;
  }
  .creo__header-text {
    font-size: 14px;
  }
  .creo__header-content_right {
    margin-top: 0em;
  }
  .creo__header-image {
    left: 10%;
  }
}
@media screen and (max-width: 500px) {
  .creo__header-content_left h1 {
    font-size: 20px;
    line-height: 30px;
  }
  creo__header-content_left p {
    font-size: 13px;
    line-height: 20px;
  }
  .creo__header-content__input input,
  .creo__header-content__input button {
    font-size: 12px;
    line-height: 16px;
  }
}/*# sourceMappingURL=header.css.map */