.ocean { 
    height: 400px;
    width:100%;
    overflow: hidden;
    position: absolute;
    bottom: 20;
    left: 0;
    opacity: 0.4;
  }

  .ocean2 {
    height: 400px;
    width:100%;
    overflow: hidden;
    position: absolute;
    left: 0;
    opacity: 0.4;
    -webkit-transform: scaleY(-1);
    transform: scaleY(-1);
  }
  
  .ocean.v01 .wave, .ocean2.v01 .wave {
      background: url(../../assets/wave.svg) repeat-x;

  }
  
  .ocean.v01, .ocean2.v01 {
      transform: rotate(180deg);
      position: absolute;
      top: 0;
      left: -200;
  }
  
  .wave {
    background: url(../../assets/wave.svg) repeat-x; 
    position: absolute;
    left: -10em;
    bottom: -25px;
    width: 12800px;
    height: 100%;
    animation: wave 7s cubic-bezier( 0.36, 0.45, 0.63, 0.53) infinite;
    transform: translate3d(0, 0, 0);
  }
  
  .wave:nth-of-type(2) {
    bottom: -35px;
    animation: wave 7s cubic-bezier( 0.46, 0.45, 0.63, 1.23) -.535s infinite, swell 7s ease -1.25s infinite;
    opacity: 1;
  }
  
  @keyframes wave {
    0% {
      margin-left: 0;
    }
    100% {
      margin-left: -6400px;
    }
  }
  
  @keyframes swell {
    0%, 100% {
      transform: translateY(-25px);
    }
    50% {
      transform: translateY(5px);
    }
  }

  @media screen and (max-width: 2100px) {
    .wave {
      width: 12800em;
    }
}

