.creo__footer {
    border: 2em solid var(--color-bg);
    color: #fff;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}

.creo__footer-heading {
    width: 100%;
    text-align: center;

    margin-bottom: 3rem;
}

.creo__footer-heading h1 {
    font-family: var(---font-family);
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 50px;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 0.2em;
}

.creo__footer-btn {
    position: absolute;
    display: flex;
    flex-direction: column;
    border: 2px solid #fff;
    text-align: center;
    border-radius: 4em;
    background-color: var(--color-blog);
    cursor: pointer;
    animation: float 8s alternate-reverse infinite;
}


@keyframes float {
	0% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translatey(0px) translateX(0px);
	}
	25% {
		box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
		transform: translatey(4em) translateX(4em);
	}
	50% {
		box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
		transform: translatey(8em) translateX(0);
	}
	75% {
		box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
		transform: translatey(4em) translateX(-4em);
	}
	100% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translatey(0em) translateX(0);
	}
}

.creo__footer-btn p {
    /* box-sizing: border-box; */
    font-family: var(---font-family);
    font-size: 24px;
    line-height: 0.3em;
    margin-top: 0;
    color: #fff;
    padding: 0 2em;
    margin: 1em 0;
    word-spacing: 2px;
}

.creo__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;

    width: 100%;
    text-align: left;
}

.creo__footer-links div {
    width: 250px;
    margin: 1rem;
}

.creo__footer-links_logo {
    display: flex;
    flex-direction: column;
}

.creo__footer-links_logo img {
    width: 100%;
    height: 100%;
    margin-bottom: 1em;
}

.creo__footer-links_logo p {
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 1.4em;
    text-transform: uppercase;
    letter-spacing: 1em;
    width: 100%;
}

.creo__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.creo__footer-links_div h4 {
    font-family: var(--font-family);
    color:black;
    font-size: 18px;
    line-height: 16px;
    color: #fff;

    margin-bottom: 1rem;
}

.creo__footer-links_div p {
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 14px;
    color: var(--color-text);

    margin: 0.5rem 0;
    cursor: pointer;
}

.creo__footer-links_div p:hover {
    text-decoration: underline;
}

.creo__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.creo__footer-copyright p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 15px;
    color: #fff;
}

@media screen and (max-width: 850px) {
    .creo__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }    
}

@media screen and (max-width: 550px) {
    .creo__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .creo__footer-links div {
        margin: 1rem 0;
    }

    .creo__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 550px) {
    .creo__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }    
}