.creo__fonctionnement {
    background:var(--color-bg);
}

.creo__fonctionnement-heading h1 {
    font-family: fantasy;
    text-shadow: 5px 5px 5px var(--color-subtext), -7px -7px -7px black;
    font-size: 5em;
    margin: 2em 0;
    width: 100%;
    
    text-transform: uppercase;
    text-align: center;
}

.creo__fonctionnement-container {
    width:90%;
}


.creo__fonctionnement-container_section {
    display: flex;
    flex-direction: row;
    width: fit-content;
  }

.creo__fonctionnement-article_img {
    width: 8%;
    height: 50em;
    object-fit:cover;
    border-radius: 40px;
    object-position: top left;
    border: 2px solid black;
    margin: 0 8px;
    cursor: pointer;
    transition: width 0.4s linear, object-fit 0.5s ease-in, object-position 0.5s ease-out;
    filter: brightness(30%);
    
    background-position: center left;
  }

.creo__fonctionnement-article_img:hover {
    width: 100%;
    scale: 1.05;
    margin: 0 35px;
    object-fit:none;
    object-position: 100% 100%;
    border: 25px solid black;
    filter: brightness(70%);
}

.creo__fonctionnement-container_section:hover .creo__fonctionnement-article_img:not(:hover) {
    object-position: -30px 0;
    width: 4%;
}

 /* Media Screen Section Options */
@media screen and (max-width: 1600px) {
    .creo__fonctionnement-container_section {
        flex-direction: column;
    }
    .creo__fonctionnement-heading h1 {
        font-size: 3em;
        text-align: center;
        
        margin-left:0 ;
    }
    .creo__fonctionnement-article_img:hover {
        width:120%;
        /* margin: 0 25px; */
        object-position: 100% 88%;
        

    }

    .creo__fonctionnement-article_img {
        width: 10%;
        height: 40em;
        object-position: top left;
        margin: 0 0.4em;

        background-position: center left;
    }

    .creo__fonctionnement-container_section:hover .creo__fonctionnement-article_img:not(:hover) {
        width:8%;
        object-position: -15px 0;
    }
}

@media screen and (max-width: 1250px) {
    .creo__fonctionnement-article_img {
        width:100%;
        height:min-content;
        object-fit: contain;
        pointer-events:none;
        transition: none;
        animation:none;
      }
      .creo__fonctionnement-article_img:hover{
        width:100%;
      }
      .creo__fonctionnement-container_section:hover .creo__fonctionnement-article_img:not(:hover) {
        width:100%;
        object-position: 0;
    }
}

@media screen and (max-width: 800px) {
    .creo__fonctionnement-heading h1 {
        padding-top: 15%;
        font-size: 2em;
        margin: 1em 0.5em;
        width: 100%;
        text-align: center;
    }
}