.creo__features-container__feature {
    /* width: 100%; */
    height:20em;
    display: flex;
    justify-content:space-between;
    align-items: flex-start;
    flex-direction: row;
    transition: scale 0.5s ease-in;
    margin: 5em 1em;
}

.creo__features-container__feature:hover {
    scale:1.12;
}


.creo__features-container__feature:hover .creo__features-container__feature-title h1{
    font-size: 35px;
}

.creo__features-container__feature:hover .creo__features-container__feature-icon {
    scale:2;
    left:-10%;
}

.creo__features-container__feature:hover .creo__features-container__feature-price text {
    transform: skew(-5deg);
    padding: 0;
    margin-left:1em;
    font-size: 20em;
}

.creo__features-container__feature-icon {
    scale: 1.2;
    margin-left: 2em;
    margin-top: 2rem;
    
    transition: scale 0.5s ease-in;
}

.creo__features-container__feature-icon img {
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
    margin-bottom: 1rem;
}

.creo__features-container__feature-title {
    flex: 1;
    margin-top: 0.5rem;
    transition: scale 0.5s ease-in;
}

.creo__features-container__feature-title h1 {
    padding-top: 0.8em;
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 28px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
    text-transform: uppercase;
    text-shadow: #0000 1px 0 10px;
    
    transition: font-size 0.5s ease-in;
}

.creo__features-container__feature-title div {
    width:100%;
    height: 2px;
    background: var(--gradient-bar);
    box-shadow: 3px 3px 3px 3px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.creo__features-container_feature-text {
    flex: 2;
    height:fit-content;
    min-width: 100px;
    display: flex;
    border: 3px solid #000;
    background-color: #031B34;
    text-align: center;
    padding: 2em;
    border-radius: 0 3em;
}

.creo__features-container_feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 20px;
    width: 100%;
    line-height: 24px;
    color: var(--color-text);
    white-space: pre-line;
    
}

.creo__features-container_feature-price {
    flex: 3; 
    height:fit-content;
    margin: 0em 2em;
    min-width: 100px;
    border-left: 2px solid black;
    border-right: 2px solid black;
    border-bottom: 2px solid black;
    padding: 2em 1em;
    border-radius: 0.1rem;
    text-align: center;
    color:  var(--color-text);
    font-family: var(--font-family);
    position: relative;
    background-color: #1a426d;
    font-weight: bold;
    border-radius: 3em 0em;
    transform: skew(-20deg);
    align-self: center;
    left: -10%;
}

.creo__features-container_feature-price text {
    padding: 1em;
    margin: 2em;
}


@media screen and (max-width: 1500px) {
    .creo__creo-feature-1 .creo__features-container_feature-price {
        left: -20%;
    }
}