.creo__contactform {
    flex: 1.5 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

.creo__contactform-label {
    width: 100%;
    display: flex;
    color: var(--color-subtext);
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    font-size: 1.2em;
    line-height: 1.2em;
    font-weight: 1000;
    font-style: oblique;
    /* margin: 1rem; */
    padding:1em 0;
    /* margin-right: 10em; */
}

.creo__contactform-img {
    width: 40%;
    margin-left: 20%;
    cursor: pointer;
    /* animation: wiggle 1.5s ease-in infinite; */
    transition: all 0.4s linear;
}

.creo__contactform-img:hover {
    scale: 1.4;
    animation: wiggle 1.5s ease-in infinite;
}

@keyframes wiggle {
    0%, 7% {
      transform: rotateZ(0);
    }
    15% {
      transform: rotateZ(-13deg);
    }
    20% {
      transform: rotateZ(9deg);
    }
    25% {
      transform: rotateZ(-10deg);
    }
    30% {
      transform: rotateZ(7deg);
    }
    35% {
      transform: rotateZ(-2deg);
    }
    40%, 100% {
      transform: rotateZ(0);
    }
  }